.footer-contacts__title {
  margin-bottom: 8px;
}
.footer-contacts__item {
  color: var(--melrose);
}
@media (min-width: 1025px) {
  .footer-contacts__link:hover {
    text-decoration: underline;
  }
}
@media (max-width: 768px) {
  .footer-contacts--address {
    grid-column: 1/-1;
  }
}

.footer-pos {
  grid-column: 1/-1;
  display: flex;
  flex-direction: column;
}